.projects {
	background-color: #14532d;
	padding: 3rem;
	color: #fff;
	text-align: center;
}

.projects h2 {
	font-size: 2.5rem;
	margin-bottom: 2rem;
}

.projects-container {
	display: grid;
	grid-template-columns: repeat(2, minmax(300px, 1fr));
	gap: 2rem;
	justify-items: center;
}

.project-card {
	background-color: #fff;
	color: #333;
	border-radius: 10px;
	width: 100%;
	box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
	overflow: hidden;
	transition: transform 0.3s ease, box-shadow 0.3s ease;
	text-align: center;
	box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);

	&:hover {
		transform: translateY(-10px);
		box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
	}

	img {
		width: 100%;
		height: auto;
		border-bottom: 5px solid #333;
	}

	.project-info {
		padding: 1.5rem;

		h3 {
			font-size: 1.5rem;
			margin-bottom: 0.5rem;
			text-align: center;
		}

		p {
			font-size: 1rem;
			margin-bottom: 1rem;
			line-height: 1.4;
		}

		.see-more {
			display: inline-flex;
			align-items: center;
			color: #333;
			text-decoration: none;
			font-weight: bold;

			&:hover {
				color: green;
			}

			.arrow {
				margin-left: 0.5rem;
				font-size: 1.2rem;
			}
		}
	}
}
